import React, {  useEffect, useState } from 'react';
import { getSecure } from '../redux/actions/app'
import Title from '../component/Title'
import Items from '../component/Items'
import Error from '../component/Error'

const Main = React.memo(
    (props) =>{
        document.title = 'Избранное'
        let [items, setItems] = useState([]);
        useEffect(async ()=>{
            let data = await getSecure({name: 'ИзбранноеПользователь'})
            if(data!=undefined)
                await setItems(data)
            if(window.rerun!=undefined)
                await window.rerun()
        },[]);
        return (
                <>
                <Title title="Избранное"/>
                {items.length>2?
                    items.map((element, idx) => {
                        if(idx!==0&&(idx+1)%3===0) {
                            return <Items history={props.history} items={[items[idx-2], items[idx-1], items[idx]]}/>
                        } else if(idx+1===items.length) {
                            if((idx+1)%2===0)
                                return <Items history={props.history} items={[items[idx-1], items[idx]]}/>
                            else
                                return <Items history={props.history} items={[items[idx]]}/>
                        }
                    })
                    :items.length>1?
                        <Items history={props.history} items={[items[0], items[1]]}/>
                        :items.length>0?
                            <Items history={props.history} items={[items[0]]}/>
                            :<Error/>}
                </>
            );
    })

export default Main;
